.App {
  text-align: center;
  max-width: 90%;
  margin: auto;
  overflow: scroll;
  font-size: 14px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.file-input {
  padding: 10px;
  cursor: pointer;
  text-align: center;
}

.column {
  display: inline-flex;
  padding: 5px;
  flex-direction: column;
  width: 65px;
}

.column button {
  margin: 5px;
}
.list-outputs {
  display: flex;
  max-width: 600px;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
  margin-bottom: 40px;
}

.activeÂ {
  width: 100px;
  height: 50px;
}

.custom-file-upload {
  width: 200px;
  padding: 50px;
  display: block;
  margin: 100px auto;
  border: 2px solid #eee;
  cursor: pointer;
}

.title {
  margin-top: 100px;
  font-weight: bold;
  border-bottom: 2px solid #ccc;
  padding: 20px;
}

h1 {
  margin-top: 100px !important;
  font-size: 2rem !important;
}

table {
  display: block;
}

th {
  font-size: 14px !important;
  padding: 5px !important;
}

.display-inline-block {
  display: inline-block;
}